@import '../../constants/colours.scss';

.about {
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: $background-color;
    padding: 2% 4%
}

.about-header-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2% 6%;
}

.about-header {
    font-weight: 400;
    font-size: 2rem;
    color: $alternate-body-text-color;
}

.about-image-container {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr 1fr;
}

.about-image {
    -webkit-flex: 0;
    flex-shrink: 0;
}

.about-text-container {
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 1.75rem;
    background-size: contain;
}

.about-text-title {
    font-size: 1.5rem;    
    text-align: center;
}

.about-text {
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 2% 2%;
}

.about-blocks-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0% 4%;
}

@media only screen and (max-width: 810px) {
    .about-image-container {
        grid-template-columns: 1fr;
    }
}